@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap');

* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
body{
    scroll-behavior: smooth;
}
.container {
    padding: 20px 100px;
}

.btn {
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1rem;
    color: #fff;
    font-weight: 700;
    line-height: 100px;
    letter-spacing: 2px;
    padding: 20px 30px;
    border-radius: 30px;
    background-color: #ff3d4a;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
}

/* Home Style */
.banner {
    width: 100%;
    height: 100vh;
    background: url('./img/bg.jpg');
    /* background-attachment: fixed; */
    background-position: center;
    background-size: cover;
}

.banner .banner-text {
    padding: 50px 100px;
    width: 50%;
}

.banner .banner-text h2 {
    font-size: 3rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    color: #fff;
}

.banner .banner-text h1 {
    margin-top: 20px;
    font-size: 4.5rem;
    font-weight: 900;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    line-height: 70px;
}

.banner .banner-text p {
    margin-top: 25px;
    font-size: 1.3rem;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

/* Navbar Style */
.nav {
    display: flex;
    justify-content: space-between;
    padding: 0px 100px;
}

.nav .logo {
    display: flex;
    flex-direction: row;
}

.nav .logo img {
    width: 80px;
    height: 80px;
    padding: 10px;
}

.nav .logo h1 {
    font-size: 1.3rem;
    padding: 25px 0px;
    font-weight: 900;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

.nav .nav-list ul {
    display: flex;
    list-style: none;
}

.nav .nav-list ul li {
    margin: 32px;
}

.nav .nav-list ul li a {
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;
    font-weight: 700;
    padding: 8px 0px;
    text-transform: uppercase;
    position: relative;
    cursor: pointer;
}

.nav .nav-list ul li a::before {
    content: '';
    position: absolute;
    background-color: #ff3d4a;
    bottom: 0%;
    left: 0%;
    width: 0%;
    height: 3px;
    transition: 0.5s;
}

.nav .nav-list ul li a:hover::before {
    width: 100%;
}

/* About Style */
.about {
    padding: 50px 0px 20px 0px;
    display: flex;
    justify-content: space-around;
}

.about .about-img {
    width: 50%;
    padding: 0px 20px;
}

.about .about-img img {
    width: 100%;
}

.about .about-text {
    width: 50%;
    padding: 0px 20px;
}

.about .about-text h3 {
    font-size: 2.5rem;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #0009;
}

.about .about-text h1 {
    margin-top: 20px;
    font-size: 3rem;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #000;
    line-height: 50px;
}

.about .about-text p {
    margin-top: 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.3rem;
    color: #0007;
}

/* Shop Style */
.shop {
    background: url('./img/bg2.jpg');
    background-attachment: fixed;
    background-position-y: -80px;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 100px 0px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.shop .box {
    margin: 0px 50px;
}

.shop .box h2 {
    font-size: 3rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

.shop .box p {
    font-size: 1.5rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

/* Menu Style */
.menu-text {
    text-align: center;
    padding: 10px 0px;
}

.menu-text h4 {
    font-size: 2rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #0009;
    margin-bottom: 20px;
}

.menu-text h2 {
    font-size: 2.6rem;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #000;
}

.menu-cards {
    display: grid;
    grid-template-columns: 350px 350px 350px;
}

.card {
    position: relative;
    width: 100%;
    padding: 10px 20px 20px 10px;
    margin: 30px;
    text-align: center;
    background-color: #fff;
    border-radius: 20px;
    transition: 0.3s;
    cursor: pointer;
}

.card .orderNow {
    position: absolute;
    background-color: #ff3d4ae0;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    transition: 0.3s;
    opacity: 0;
}

.card .orderNow a {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    padding: 68% 13%;
    font-size: 2.4rem;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    text-decoration: none;
}

.card:hover {
    box-shadow: 0px 0px 20px #0002;
}

.card:hover .orderNow {
    opacity: 1;
}

.card img {
    width: 90%;
}

.card h3 {
    font-size: 1.5rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #000;
    padding-bottom: 20px;
}

.card p {
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    color: #0007;
    padding-bottom: 20px;
}

.card h4 {
    font-size: 2rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #000;
}

/* Contact Us Style */
.contact {
    position: relative;
    background: url('./img/contact.jpg');
    background-position: center;
    background-size: cover;
    padding: 60px 100px;
}
.contact h3{
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 20px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}
.contact form {
    width: 40%;
    text-align: center;
    background: transparent;
}
.contact form input, textarea{
    padding: 20px;
    margin: 10px 0px;
    width: 100%;
    border: 2px solid #ff3d4a;
    border-radius: 12px;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    background: transparent;
}
.contact form input, textarea:focus{
    outline: none;
}
.contact form input::placeholder{
    color: #fff;
}
.contact form textarea::placeholder{
    color: #fff;
}
.contact form #chat{
    font-size: 1.2rem;
    font-weight: 700;
    background: #ff3d4a;
    text-transform: uppercase;
    cursor: pointer;
}
.contact form input:focus{
    background-color: #ff3d4a5b;
}
.contact form textarea:focus{
    background-color: #ff3d4a5b;
}
.contact form input:-webkit-autofill,
.contact form input:-webkit-autofill:hover,
.contact form input:-webkit-autofill:focus{
    box-shadow: 0 0 0 50px #bcc2c9ad inset !important;
}



/* Footer Style */
.footer{
    background-color: #ff3d4a;
    padding: 20px 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.footer h4{
    font-size: 1.6rem;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #000;
}
.footer .links .f-list ul{
    list-style: none;
}
.footer .links .f-list ul li{
    padding: 5px 0px;
}
.footer .links .f-list ul li a{
    text-decoration: none;
    font-size: 1rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}
.footer .location p{
    text-decoration: none;
    font-size: 1rem;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    line-height: 25px;
}
.footer .scl-media ul{
    list-style: none;
}
.footer .scl-media ul li Link{
    text-decoration: none;
    font-size: 2.5rem;
    color: #fff;
    cursor: pointer;
}

.mobile-nav{
    display: none;
}
.nav .nav-list ul .close-btn{
    display: none;
}
 
@media only screen and (max-width : 460px) {
    .container{
        padding: 10px;
    }
    .nav{
        padding: 10px;
    }
    .nav .logo img{
        width: 65px;
        height: 65px;
        padding: 0%;
    }
    .nav .logo h1{
        font-size: 1rem;
    }
    .nav .nav-list ul {
        display: none;
        position: fixed;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        transform: translate(-50%,-50%);
        background-color: #ff3d4ac0;
        text-align: center;
        padding-top: 50%;
        z-index: 99;
    }
    .nav .nav-list ul .close-btn{
        position: absolute;
        top: 25px;
        right: 30px;
        font-size: 1.7rem;
        font-weight: 700;
        color: #fff;
        font-family: 'Montserrat', sans-serif;
        text-decoration: none;
    }
    .nav .nav-list ul li a{
        font-size: 1.5rem;
        font-weight: 800;
    }
    .nav-list .mobile-nav{
        display: block;
        padding: 20px;
    }
    .nav-list .mobile-nav .menu-icon{
        font-size: 1.5rem;
        color: #fff;
        text-decoration: none;
    }
    .banner .banner-text{
        width: 90%;
        padding: 10px;
        margin: 30px 0px 0px 20px;
    }
    .banner .banner-text h2{
        font-size: 2rem;
    }
    .banner .banner-text h1{
        font-size: 2.5rem;
        line-height: 40px;
    }
    .banner .banner-text p{
        width: 65%;
    }
    .about{
        display: block;
        padding: 20px 0px 20px 0px;
    }
    .about .about-img{
        width: 100%;
        margin-bottom: 20px;
    }
    .about .about-text{
        width: 100%;
    }
    .about .about-text h1{
        font-size: 1.5rem;
        line-height: 40px;
    }
    .shop{
        background-position-y: 0px;
        display: block;
        padding: 30px 0px;
    }
    .shop .box{
        margin: 50px 0px;
    }
    .menu-text h4{
        font-size: 1.8rem;
    }
    .menu-text h2{
        font-size: 2rem;
    }
    .menu-cards {
        display: block;
    }
    .card{
        margin: 0px;
        padding: 40px 20px;
    }
    .contact{
        padding: 40px;
    }
    .contact form{
        width: 100%;
    }
    .footer{
        flex-direction: column;
        padding: 40px;
    }
}